/* 스크롤바 없애기 */
body{
  -ms-overflow-style: none;
  overflow: auto;
  }
  
 ::-webkit-scrollbar {
   display: none;
 }


li {
  list-style:none;
}

img {
  width: 100%;
  vertical-align: top;
}

button{
  color: white;
  background-color: black;
  height: 30px;
}

.toonimg{
  width: 100%;
  font-size: 0;
  position: absolute;
  z-index: -2;
}

.grid {
  z-index: -1;
  position: absolute;
  top: 0;
  width: 100%;
  background-image: url('./grid.png');
  background-repeat: repeat-y;
  background-size: 100vw;
}

.pincoms_gridbox {
  position: absolute;
  z-index: -1;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 25vw;
}
