/* 하트 색 차기안차기 */
.like {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-right: 16px;
}

.empty {
    opacity: 0.1;
}

.fill{
    opacity: 0.4;
}

/* 여기 부터 남의 핀컴 */

.pincom1 {
    background-color: rgba(33, 33, 44, 0.8);
    line-height: 120%;
    /* width: 234px; */
  }

.topbar{
    height: 2px;
    width: calc(100% - 17px);
    /* margin: 4px auto 16px; */
}
.topbar li{
    display: inline-block;
}

.txtBox {
    /* margin: 0 16px; */
    margin: 0 6px;
}
.prof1 {
    padding: 0;
}
.prof1 li{
    display: inline-block;
    margin-right: 4px;
}
.profImg{
    width: 16px;  
}
.nam1 {
    font-size: 12px;
    font-weight: 600;

}

.num1{
    font-size: 10px;
    font-weight: 500;
    opacity: 0.4;
}

.com1 {
    /* margin:8px 0; */
    margin:2px 0;
    font-style: Medium;
    line-height: 150%;
}
.reply1 {
    padding: 0;
    /* margin: 0 16px; */
    margin: 0 4px;
    border-top: 1px solid rgba(252, 252, 252, 0.1);
}
.reply1 li{
    display: inline-block;
    width: 50%;
    font-size: 6px;
    font-weight: 600;
    text-align: center;
    margin: 12px auto;
    margin: 5px auto;
   color: rgba(252, 252, 252, 0.4);
}

.reply1 li:first-child{
    border-right: 1px solid rgba(252, 252, 252, 0.1);
}